.d-flex {
  display: flex;
}
.b-bottom {
  border-bottom: 1px solid black;
}
.css-price {
  color: #a45b20;
  font-weight: bold;
  font-size: 19px;
}
.text {
  font-style: italic;
}
.btn-add {
  text: center;
  color: white;
  background-color: #68370f;
  border: none;
  border-radius: 10px;
  padding: 5px 20px 5px 20px;
}
.btn-add:hover,
.btn-add:active {
  background-color: #8a2b06;
}
.b-amount {
  border: 1px solid gray;
  border-radius: 5px;
  padding: 1px 15px 1px 15px;
  margin-left: 10px;
}

.icon {
  width: 1.35rem;
  height: 1.35rem;
  margin-right: 0.5rem;
}

.bg-container {
  background-color: #383838;
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 10;
  background: rgba(0, 0, 0, 0.75);
  display: flex;
  align-items: center;
  justify-content: center;
}

.modal {
  max-width: 80%;
  max-height: 80%;
  overflow-y: auto;
  background-color: white;
  border-radius: 8px;
  padding: 20px;
}

.h-container-modal {
  display: flex;
  justify-content: space-between;
}

@media (min-width: 768px) {
  .modal {
    left: calc(50% - 20rem);
    width: 40rem;
  }
}
