
.container{
  text-align: center;
  max-width: 45rem;
  width: 90%;
  margin: auto;
  margin-top: 4rem;
  padding-bottom: 4rem;
  position: relative;
  background-color: #ffff;
  border-radius: 14px;
  padding: 1rem;
  box-shadow: 0 1px 18px 10px rgba(0, 0, 0, 0.25);
  animation: meals-appear 0.5s ease-out forwards;
}
.meals {
  display: flex;
  justify-content: space-between;
  max-width: 60rem;
  width: 90%;
  margin: 2rem auto;
  
}

.meals ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

@keyframes meals-appear {
  from {
    opacity: 0;
    transform: translateY(3rem);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}
